#chat-message-list {
    grid-area: chat-message-list;
    display: flex;
    flex-direction: column-reverse;
    padding: 0 20px;
    overflow-y: scroll;
  }
  

.message-row {
    display: grid;
    grid-template-columns: 100%;
    margin-bottom: 20px;
}
  
.message-content {
    display: grid;
}
  
.you-message {
    justify-content: end;
}
  
.you-message .message-content {
    justify-items: end;
}
  
.other-message {
    justify-items: start;
}
  
.other-message .message-content {
    grid-template-columns: 48px 1fr;
    grid-column-gap: 15px;
}
  
.message-row img {
    border-radius: 100%;
    grid-row: span 2;
}
  
.message-time {
    font-size: 1.3rem;
    color: #777;
}
  
.message-text {
    padding: 9px 14px;
    font-size: 1.6rem;
    margin-bottom: 5px;
}
  
.you-message .message-text {
    background: #0048AA;
    color: #eee;
    border: 1px solid #0048AA;
    border-radius: 14px 14px 0 14px;
}
  
.other-message .message-text {
    background: #eee;
    color: #111;
    border: 1px solid #ddd;
    border-radius: 14px 14px 14px 0;
}