.header {
    max-width: 1500px;
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #253b79;

    padding: 0 5%;
  }
  
  .logo {
    margin-right: 50px;
    position: relative;
    right: 40px;
    font-size: 1.5rem;
    color:#E2C2F7;
    font-weight: 400;
  }
  .icon {
    width: 30px;
    height: 30px;
  }
  .icon:hover {
    filter: invert(91%) sepia(21%) saturate(1500%) hue-rotate(192deg) brightness(93%) contrast(110%);
  }
  .filter {
    filter: invert(64%) sepia(35%) saturate(496%) hue-rotate(167deg) brightness(91%) contrast(88%);
  }
  .menu {
    cursor: pointer;
    margin-right: 10px;
    margin-left: 10px;
  }
  
  .header ul {
    list-style: none;
    font-weight: 300;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
  }
  
  .header li {
    margin-left: 3rem;
  }
  
  .header a {
    text-decoration: none;
    font-size: 1rem;
    color: #70a3d2;
  }
  
  .header a:hover,
  .header a:active,
  .header a.active {
    color: #E2C2F7;
  }
  
  
